	module.exports = {"data":{"post":{"html":"<p>Best wishes to Roland and Kirsten, Mama's brother and sister-in-law, who announced to the family today that they are expecting a 3rd child in November. </p>\n<p>In the spirit of stressed-out parents, I've uploaded a new layout that's only half finished. I've stolen the Tagesmütter logo; it seems to fit us somehow. I still need to work out where to put the calendar and the favorite links. Putting text over the colored stripes is not a good option</p>\n<p>Christopher's word of the day is 'deng'. This apparently is an exclamation to be made when any object, including Christopher himself, falls to the floor with a thud. Deng.</p>","fields":{"title":"Deng","date":"17 Jun 2001","url":"https://papascott.de/2001-06-17-deng/"}}},"pathContext":{"slug":"/2001-06-17-deng/","prev":{"fields":{"title":"New Doo","date":"16 Jun 2001","slug":"/2001-06-16-new-doo/"}},"next":{"fields":{"title":"Happy Monday","date":"18 Jun 2001","slug":"/2001-06-18-happy-monday/"}}}}

